<template>
    <div class="joke-container">
        <p class="joke">{{joke.setup}}</p>
        <p class="punch">{{joke.punchline}}</p>
        <span class="close" @click="removeJoke(index)">&times;</span>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  props: ['joke', 'index'],
  methods: mapActions([
    'removeJoke'
  ])
}
</script>
<style lang="scss">
    .joke-container{
        border: 1px solid rgba(107, 103, 103, 0.582);
        margin: 20px 0;
        min-height: 120px;
        padding: 10px 30px;
        color: rgb(87, 87, 87);
        position: relative;
        border-radius: 6px;
        .close{
            position: absolute;
            right: 8px;
            top: 10px;
            padding: 0 4px;
            transform: scale(1.8);
            cursor: pointer;
            &:hover{
                opacity: .9;
                transform: scale(2);
            }
        }
        &:hover .punch{
            opacity: 1;
        }
        .punch{
            opacity: 0;
            transition: opacity .5s ease-out;
        }
        p{
            text-align: center;
            font-size: 1.3em;
            &:first-of-type{
                margin-bottom: 15px;
                color: rgb(49, 48, 54);
            }
        }
    }
</style>
